    selectedCategory: "",
    isSmallScreen: false,
  }),

  components: {
    CardInfo,
  },

  computed: {
    isComponentAActive() {
      return this.$route.name === 'CardInfoDialog';
    },
    Search: {
      get() {
        return this.searchValue;
      },
      set(value) {
        this.searchValue = value ?? "";
      },
    },
    cartButtonStyle() {
    return this.$vuetify.breakpoint.width < 816
        ? {
            position: 'fixed',
            zIndex: 9999,
            right: '-30px',
            bottom: '12px',
          }
        : {
            position: 'fixed',
            zIndex: 9999,
            right: '-30px',
            top: '20px',
          };
    },
  },

  mounted() {
    this.filteredCards = this.cardList;
    this.isSmallScreen = window.innerWidth < 480;
    window.addEventListener('resize', this.handleResize);
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener("scroll", this.handleScroll);
  },

  methods: {
    handleScroll() {
      this.showCartButton = window.scrollY > 450;
    },
    cartDialog() {
      this.cartDialogVisible = true;
      EventBus.$emit('open_basket', this.cartDialogVisible);
      this.cartDialogVisible = false;
    },
    filterByCategory(category) {
      this.selectedCategory = category;
      this.filteredCards = this.cardList.filter(card => 
        (!category || card.category === category) &&
        (!this.Search || card.name.toLowerCase().includes(this.Search.toLowerCase()))
      );
    },
    handleResize() {
      this.isSmallScreen = window.innerWidth < 480;
    },
  },

  watch: {
    Search: {
      deep: true,
      handler: _.debounce(function () {
        const searchTerm = this.Search.toLowerCase();
        this.filteredCards = this.cardList.filter(card =>
          (!this.selectedCategory || card.category === this.selectedCategory) &&
          (!searchTerm || card.name.toLowerCase().includes(searchTerm) ||
          card.numberId.toLowerCase().includes(searchTerm))
        );
      }, 400),
    }
  }
}
