<style scoped>
.cart-button {
  background-color: white; /* Білий задній фон */
  border: 2px solid green; /* Опціонально: рамка */
  border-radius: 50%; /* Якщо кнопка має бути круглою */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2); /* Легка тінь */
  transition: background-color 0.3s ease, transform 0.2s ease;
}
.cart-button:hover {
  background-color: #f5f5f5; /* Світло-сірий фон при наведенні */
  transform: scale(1.05); /* Легке збільшення при наведенні */
}
.category-btn {
  display: flex;
  align-items: center;
}
.ifcenter {
  justify-content: end;
}
@media (max-width: 480px) {
  .ifcenter {
    display: flex;
    justify-content: center;
  }
}
</style>
