<template>
  <v-card width="365px" @click="linksDialog = true">
    <v-list>
      <v-list-item-title class="justify-center d-flex mb-2" style="font-size: 20px;">
        {{ card.name }}
      </v-list-item-title>
      <v-list-item>
        <v-img style="width: 332px; height: 180px;" :src="card.url" />
      </v-list-item>
     <div class="d-flex justify-space-between">
      <v-list-item>Номер: {{ card.numberId }}</v-list-item>
      <v-list-item class="d-flex justify-end">Ціна: {{ card.price }}</v-list-item>
     </div>
     <v-btn 
       class="ml-4" 
       color="green" 
       style="color: whitesmoke !important; text-transform: none;"
       @click.stop="addToCart"
     >
       До кошика
     </v-btn>
    </v-list>

    <CardInfoDialog
      :visible="linksDialog"
      @close="linksDialog = false"
      :projectId="card"
    />
  </v-card>
</template>

<script>
import CardInfoDialog from './CardInfoDialog.vue';
import { EventBus } from './eventBus.js';

export default {
  name: 'CardInfo',

  data() {
    return {
      linksDialog: false,
    };
  },

  props: {
    card: {
      type: Object,
      required: true,
    },
  },

  methods: {
    addToCart() {
      EventBus.$emit('add-to-cart', this.card);
    }
  },

  components: {
    CardInfoDialog,
  },
};
</script>
