<template>
  <v-dialog
    v-model="localVisible"
    max-width="800"
    content-class="cross__dialog"
  >
    <v-card>
      <v-fade-transition>
        <v-progress-linear
          v-if="loading"
          absolute
          top
          indeterminate
          color="orange"
        ></v-progress-linear>
      </v-fade-transition>
      <v-slide-x-transition>
        <div class="cross__inner">
          <v-btn fab small elevation="0" @click="onCancel">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </div>
      </v-slide-x-transition>
      <v-row class="ma-0 pa-0" style="min-height: auto;">
        <v-col md="8">
          <v-img class="rounded" style="max-height: 60vh" :src="projectId.url" />
        </v-col>
        <v-col md="4" class="d-flex flex-column align-start pt-7">
          <h2>{{ projectId.name }}</h2>
          <h4 class="my-2">Ціна: {{ projectId.price }}</h4>
          <h4 class="mb-2">
            Категорія: {{ projectId.category }}
          </h4>
          <h4 class="mt-2 mb-10">
            Параметри: {{ projectId.description }}
          </h4>
          <v-btn 
            class="mr-auto mt-auto" 
            color="green" 
            style="color: whitesmoke !important; text-transform: none;"
            @click.stop="addToCart"
          >
            До кошика
          </v-btn>
        </v-col>

      </v-row>
    </v-card>
  </v-dialog>
</template>
    
<script>
import { EventBus } from './eventBus';

export default {
  name: 'CardInfoDialog',

  props: {
    visible: {
      type: Boolean,
      required: true,
    },
    projectId: {
      type: Object,
      required: false,
    },
  },

  data() {
    return {
      loading: false,
      localVisible: this.visible,
    };
  },

  watch: {
    visible(val) {
      this.localVisible = val;
    },
    localVisible(val) {
      if (!val) {
        this.$emit('close');
      }
    }
  },

  methods: {
    addToCart() {
      EventBus.$emit('add-to-cart', this.projectId);
    },
    onCancel() {
      this.localVisible = false;
    },
  },
};
</script>


<style>

@media (max-width: 600px) {
  .v-col-custom {
    flex-basis: 50%; /* Наприклад, займає половину ширини */
    max-width: 50%;
  }
}


.cross__dialog {
  &.v-dialog {
    overflow-y: unset !important ;
  }
}
.cross__inner {
  position: absolute;
  top: -10px;
  right: -10px;
  z-index: 100;
  transition: 0.2s ease;
  &:hover {
    //transform: rotate(180deg);
  }
  &:active {
    transform: scale(0.9);
  }
}
</style>