<template>
  <v-card>
    <!-- Картинка з текстом -->
    <v-row class="justify-center">
      <v-col cols="12" class="pt-0">
        <div class="image-container">
          <v-img
            src="../../public/images/tovar/titleimg.jpg"
            class="image"
          >
            <div class="overlay">
              <div class="text-overlay">
                <v-row class="justify-space-between align-center mx-5">
                  <v-col cols="auto" class="title-container">
                    <h2 class="title">Косівський розпис</h2>
                  </v-col>
                  <div class="d-flex" style="gap: 5px;">
                    <v-col cols="auto">
                      <v-btn icon large outlined color="green" @click="cartDialog = true" class="cart-button">
                        <v-icon color="green">mdi-cart</v-icon>
                        <div v-if="cart.length > 0" class="cart-indicator"></div>
                      </v-btn>

                      <v-dialog v-model="cartDialog" max-width="700px">
                        <v-card>
                          <v-card-title style="border-bottom: 2px solid #9e9e9e;">Ваш кошик</v-card-title>
                          <div v-if="cart.length <= 0" style="font-size: 22px;" class="d-flex align-center justify-center mt-5">Кошик пустий</div>
                          <v-card-text>
                            <v-list>
                              
                              <v-list-item v-for="(item, index) in cart" :key="index" class="pa-2 mb-2" style="border-bottom: 2px solid #9e9e9e;">
                                <v-img style="max-width: 100px; max-height: 100px; border-radius: 10px;" :src="item.url" class="mr-4 elevation-2"/>
                                <div class="d-flex align-center justify-space-between flex-wrap" style="width: 100%;">

                                  <div class="d-flex flex-column mr-4">
                                    <v-list-item-title style="font-size: 19px;">{{ item.name }}</v-list-item-title>
                                    <v-list-item-subtitle style="font-size: 16px;">Номер :{{ item.numberId }}</v-list-item-subtitle>
                                  </div>
                                
                                  <v-col cols="auto" class="mr-4">
                                    <v-text-field
                                      v-model="item.quantity"
                                      type="number"
                                      min="1"
                                      max="99"
                                      class="custom-quantity-field"
                                      outlined
                                      dense
                                      hide-details
                                    >
                                    </v-text-field>
                                  </v-col>
                                  
                                  <div class="d-flex align-center">
                                    <v-list-item style="font-size: 19px;">{{ item.price }}</v-list-item>

                                    <v-btn icon @click="removeFromCart(index)">
                                      <v-icon color="red">mdi-close</v-icon>
                                    </v-btn>
                                  </div>
                                </div>


                              </v-list-item>
                            </v-list>
                          </v-card-text>

                          <v-form v-if="cart.length > 0" ref="form" v-model="formValid">
                            <v-row class="mx-2 my-0 pa-0 align-center justify-center">
                              <v-col :cols="$vuetify.breakpoint.width <= 500 ? '12':'6'" class="ma-0 py-0">
                                <v-text-field 
                                  v-model="InputUserInfo.user_name"
                                  outlined
                                  label="Ім'я"
                                  dense
                                  :maxlength="50"
                                  :rules="[v => !!v || 'Введіть ім\'я']"
                                />
                              </v-col>
                            
                              <v-col :cols="$vuetify.breakpoint.width <= 500 ? '12':'6'" class="ma-0 py-0">
                                <v-text-field 
                                  v-model="InputUserInfo.user_surname"
                                  outlined
                                  label="Прізвище"
                                  dense
                                  :maxlength="50"
                                  :rules="[v => !!v || 'Введіть прізвище']"
                                />
                              </v-col>
                            
                              <v-col :cols="$vuetify.breakpoint.width <= 500 ? '12':'6'" class="ma-0 py-0">
                                <v-text-field 
                                  v-model="InputUserInfo.user_phone"
                                  outlined
                                  label="Номер телефону"
                                  dense
                                  :maxlength="50"
                                  :rules="[v => !!v || 'Введіть номер телефону']"
                                />
                              </v-col>
                            
                              <v-col :cols="$vuetify.breakpoint.width <= 500 ? '12':'6'" class="ma-0 py-0">
                                <v-text-field 
                                  v-model="InputUserInfo.user_adress"
                                  outlined
                                  label="Адреса доставки"
                                  dense
                                  :maxlength="100"
                                  :rules="[v => !!v || 'Введіть адресу доставки']"
                                />
                              </v-col>
                            
                              <v-col :cols="$vuetify.breakpoint.width <= 500 ? '12':'12'" class="ma-0 py-0">
                                <v-textarea
                                  v-model="InputUserInfo.user_description"
                                  outlined
                                  rows="4"
                                  label="Опис"
                                  dense
                                  hide-details="auto"
                                  auto-grow
                                  :maxlength="1000"
                                />
                              </v-col>
                            
                            </v-row>
                          </v-form>
                          <v-card-actions class="justify-end">
                            <v-btn  @click="cartDialog = false" outlined color="red" style="text-transform: none;">Закрити</v-btn>
                            <v-btn :disabled="!BtnOn" v-if="cart.length > 0" color="green" style="color: whitesmoke; text-transform: none;" @click="checkout">Оформити замовлення</v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>

                      <!-- <v-chip
                        class="px-5"
                        color="green"
                        text-color="white"
                      >
                        <v-avatar
                          left
                          class="green darken-4"
                        >
                          2
                        </v-avatar>
                        <v-icon>
                          mdi-basket
                        </v-icon>
                      </v-chip> -->
                    </v-col>
                  </div>
                </v-row>
              </div>
            </div>
            <div class="overlay-text">
              <h1>Косівська кераміка, на замовлення</h1><br/>
              <h1>Джаман Андрій</h1>
              <h2>+(380)98-476-4371</h2>
              <h2>+(380)93-720-6307</h2>
            </div>
          </v-img>
        </div>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import { EventBus } from './eventBus.js';
import _ from 'lodash';
// import axios from 'axios';

export default {
  name: 'headerPage',
  data: () => ({
    name: "First code",
    cart: [],
    cartDialog: false,
    BtnOn: false,
    InputUserInfo: {
      user_name: "",
      user_surname: "",
      user_phone: "",
      user_adress: "",
      user_description: "",
    },
    formValid: false,
    CopyInputUserInfo: {},

    selectedAddress: null,
    addresses: [],
    query: "",
    loading: false,
    page: 1,
    hasMore: true,
  }),
  props: {
    basket_num: {
      type: Number,
      require: false
    }
  },
  mounted() {
    this.CopyInputUserInfo = {...this.InputUserInfo};
  },
  created() {
    EventBus.$on('add-to-cart', this.addToCart);
    EventBus.$on('open_basket', this.openCartDialog);
  },
  beforeDestroy() {
    EventBus.$off('add-to-cart', this.addToCart);
    EventBus.$off('open_basket', this.openCartDialog);
  },
  methods: {
    openCartDialog() {
    this.cartDialog = true; // Встановлюємо значення true при отриманні події
  },
    // async fetchAddresses(loadMore = false) {
    //   if (this.loading || (!this.hasMore && loadMore)) return;
// 
    //   this.loading = true;
    //   try {
    //     const response = await axios.post("https://api.novaposhta.ua/v2.0/json/", {
    //       apiKey: process.env.VUE_APP_NOVA_POSHTА_API_KEY,
    //       modelName: "Address",
    //       calledMethod: "getSettlements",
    //       methodProperties: {
    //         FindByString: this.query,
    //         Page: this.page,
    //         Limit: 15,
    //       },
    //     });
// 
    //     const newAddresses = response.data.data || [];
    //     if (loadMore) {
    //       this.addresses = [...this.addresses, ...newAddresses];
    //     } else {
    //       this.addresses = newAddresses;
    //     }
// 
    //     this.hasMore = newAddresses.length > 0; // Перевірка, чи залишилися ще дані
    //     if (this.hasMore) {
    //       this.page += 1; // Збільшення номера сторінки
    //     }
    //   } catch (error) {
    //     console.error("Помилка підвантаження адрес:", error);
    //   } finally {
    //     this.loading = false;
    //   }
    // },
// 
    // onSearchInput(query) {
    //   this.query = query;
    //   this.page = 1;
    //   this.hasMore = true;
    //   this.addresses = []; // Очистити список для нового запиту
    //   this.fetchAddresses();
    // },
// 
    // onScroll(event) {
    //   const element = event.target;
    //   if (element.scrollTop + element.clientHeight >= element.scrollHeight) {
    //     // Коли прокрутка досягає нижньої межі
    //     this.fetchAddresses(true); // Підвантаження нових даних
    //   }
    // },
    addToCart(item) {
      const foundItem = this.cart.find(cartItem => cartItem.id === item.id);
      if (foundItem) {
        foundItem.quantity++;
      } else {
        this.cart.push({ ...item, quantity: 1 });
      }
    },
    removeFromCart(index) {
      this.cart.splice(index, 1);
    },
    async checkout() {
      const form = this.$refs.form;

      if (!form.validate()) {
        return;
      }

      const botToken = process.env.VUE_APP_NOVA_TOKEN_TELEGRAM_API_KEY;
      const chatId = process.env.VUE_APP_NOVA_ID_TELEGRAM_API_KEY;

      let message = "Замовлення:\n";

      this.cart.forEach((item, index) => {
        message += `${index + 1}. Номер: ${item.numberId}, ${item.name}\nКількість: ${item.quantity}, Ціна: ${item.price}\n`;
      });

      message += `\nІнформація про користувача:\n`;
      message += `Ім'я: ${this.InputUserInfo.user_name}\n`;
      message += `Прізвище: ${this.InputUserInfo.user_surname}\n`;
      message += `Номер телефону: ${this.InputUserInfo.user_phone}\n`;
      message += `Адреса доставки: ${this.InputUserInfo.user_adress}\n`;
      message += `Опис: ${this.InputUserInfo.user_description}\n`;

      try {
        await fetch(`https://api.telegram.org/bot${botToken}/sendMessage`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            chat_id: chatId,
            text: message,
          }),
        });
        alert("Замовлення успішно оформлено! Очікуйте на зворотній зв'язок!");
      } catch (error) {
        console.error("Помилка відправлення:", error);
        alert("Не вдалося відправити замовлення спробуйте ще раз.");
      }

      this.cartDialog = false;
    }
  },
  watch: {
    InputUserInfo: {
      deep: true,
      handler() {
        this.BtnOn = !_.isEqual(this.CopyInputUserInfo, this.InputUserInfo);
      }
    }
  },
}
</script>

<style scoped>
.cart-button {
  position: relative;
}

.cart-indicator {
  position: absolute;
  top: -8px;
  right: -4px;
  width: 15px;
  height: 15px;
  background-color: orange;
  border-radius: 50%;
}
.custom-quantity-field {
  width: 80px;
  max-width: 80px;
}

.v-text-field__control {
  border-radius: 8px;
}

.v-input__control .v-field__append-inner {
  display: flex;
  align-items: center;
}
.image-container {
  position: relative;
  width: 100%;
}

.image {
  width: 100%;
  height: auto;
  object-fit: cover;
  aspect-ratio: 3.5;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.568); /* Легке затемнення для всієї картинки */
}

.text-overlay {
  background-color: rgba(0, 0, 0, 0.7); /* Більш сильне затемнення для тексту */
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.title {
  color: white;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  font-size: 24px; /* Розмір тексту по замовчуванню */
}

.overlay-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
  font-size: 15px;
  padding: 10px;
}

@media (max-width: 1080px) {
  .overlay-text {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    text-align: center;
    font-size: 10px;
  }
}

@media (max-width: 492px) {
  .image {
    aspect-ratio: 1.2;
  }
}

@media (max-width: 552px) and (min-width: 492px) {
  .image {
    aspect-ratio: 1.8;
  }
}

@media (max-width: 690px) and (min-width: 552px) {
  .image {
    aspect-ratio: 1.9;
  }
}

@media (max-width: 870px) and (min-width: 690px) {
  .image {
    aspect-ratio: 2.5;
  }
}

@media (max-width: 600px) {
  .text-overlay {
    padding: 10px;
    gap: 5px;
  }

  .title {
    font-size: 18px; /* Зменшення розміру тексту на мобільних */
    text-align: center;
  }

  .v-chip {
    font-size: 14px; /* Зменшення розміру тексту в чипах */
    padding: 5px;
  }

  .v-avatar {
    font-size: 14px; /* Зменшення розміру аватара */
  }
}
</style>
